import { ResponsiveDeviceName, TableState } from "@wptb/dev/types";
import { TimeTravelEntry } from "./capturer";
import { toResponsive } from "../table/responsive";

interface PropsUpdate {
  blockId: string;
  from: Record<string, any>;
  to: Record<string, any>;
}
type ChangeFn = (
  state: TableState,
  changes: TimeTravelEntry | PropsUpdate,
  device?: ResponsiveDeviceName
) => void;

export const undoChanges: ChangeFn = (state, changes, device) => {
  if ((changes as PropsUpdate).blockId) {
    const { blockId, from } = changes as PropsUpdate;
    state.blocksProps[blockId] = from;
    return;
  }

  const table = state.desktop;
  const innerBlocks = state.innerBlocks;
  const blocksProps = state.blocksProps;
  const {
    blockPass,
    removedBlocks,
    addedBlocks,
    movedBlocks,
    addedProps,
    removedProps,
    updatedProps,
    addedRows,
    removedRows,
    addedCells,
    removedCells,
    rowChanges,
    rowOrder,
    colOrder,
    shouldRenderResponsive,
  } = changes as TimeTravelEntry;

  for (const blockId in updatedProps) {
    const { from } = updatedProps[blockId];
    blocksProps[blockId] = from;
  }
  for (const blockId in addedProps) {
    delete blocksProps[blockId];
  }
  for (const blockId in removedProps) {
    blocksProps[blockId] = removedProps[blockId];
  }

  for (const rowId of addedRows) {
    table.blockOrder = table.blockOrder.filter((blockId) => blockId !== rowId);
    delete table.innerBlocks[rowId];
  }

  for (const rowId of removedRows) {
    const { id, index, blockOrder } = rowChanges[rowId];
    table.blockOrder.splice(index, 0, id);
    table.innerBlocks[id] = blockOrder;
  }

  if (rowOrder) {
    table.blockOrder = rowOrder.from;
  }

  if (colOrder) {
    table.innerBlocks = colOrder.from;
  } else {
    for (const cell of addedCells) {
      const { rowId, id } = cell;
      if (!rowChanges[rowId]) {
        table.innerBlocks[rowId] = table.innerBlocks[rowId].filter(
          (blockId) => blockId !== id
        );
      }
      delete innerBlocks[id];
    }

    for (const cell of removedCells) {
      const { rowId, index, id } = cell;
      if (!rowChanges[rowId]) {
        table.innerBlocks[rowId].splice(index, 0, id);
      }
      innerBlocks[id] = blockPass[id].block;
    }
  }

  for (const blockId of addedBlocks) {
    delete innerBlocks[blockId];
    const { block } = blockPass[blockId];
    if (!blockPass[block.parentId]) {
      const parent = innerBlocks[block.parentId];
      parent.blockOrder = parent.blockOrder.filter((id) => id !== blockId);
    }
  }

  for (const blockId of removedBlocks) {
    const { block, index } = blockPass[blockId];
    innerBlocks[blockId] = block;
    if (!blockPass[block.parentId]) {
      innerBlocks[block.parentId].blockOrder.splice(index, 0, blockId);
    }
  }

  for (const moved of movedBlocks) {
    const { id, from, to, fromIndex, toIndex } = moved;
    innerBlocks[id].parentId = from;

    if (from === to) {
      const parent = innerBlocks[from].blockOrder;
      const f = parent[fromIndex];
      parent[fromIndex] = parent[toIndex];
      parent[toIndex] = f;
      continue;
    }

    if (!blockPass[to]) {
      innerBlocks[to].blockOrder = innerBlocks[to].blockOrder.filter(
        (blockId) => blockId !== id
      );
    }
    if (!blockPass[from]) {
      innerBlocks[from].blockOrder.splice(fromIndex, 0, id);
    }
  }

  if (shouldRenderResponsive) {
    state.mobile = table;
    state.tablet = table;
    if (device && device !== "desktop") {
      state[device] = toResponsive(state, device);
    }
  }
};

export const redoChanges: ChangeFn = (state, changes, device) => {
  if ((changes as PropsUpdate).blockId) {
    const { blockId, to } = changes as PropsUpdate;
    state.blocksProps[blockId] = to;
    return;
  }

  const table = state.desktop;
  const innerBlocks = state.innerBlocks;
  const blocksProps = state.blocksProps;

  const {
    blockPass,
    removedBlocks,
    addedBlocks,
    movedBlocks,
    addedProps,
    removedProps,
    updatedProps,
    addedRows,
    removedRows,
    rowChanges,
    rowOrder,
    colOrder,
    addedCells,
    removedCells,
    shouldRenderResponsive,
  } = changes as TimeTravelEntry;

  for (const blockId in updatedProps) {
    const { to } = updatedProps[blockId];
    blocksProps[blockId] = to;
  }
  for (const blockId in addedProps) {
    blocksProps[blockId] = addedProps[blockId];
  }
  for (const blockId in removedProps) {
    delete blocksProps[blockId];
  }

  for (const rowId of removedRows) {
    table.blockOrder = table.blockOrder.filter((blockId) => blockId !== rowId);
    delete table.innerBlocks[rowId];
  }

  for (const rowId of addedRows) {
    const { id, index, blockOrder } = rowChanges[rowId];
    table.blockOrder.splice(index, 0, id);
    table.innerBlocks[id] = blockOrder;
  }

  if (rowOrder) {
    table.blockOrder = rowOrder.to;
  }

  if (colOrder) {
    table.innerBlocks = colOrder.to;
  } else {
    for (const cell of removedCells) {
      const { rowId, id } = cell;
      if (!rowChanges[rowId]) {
        table.innerBlocks[rowId] = table.innerBlocks[rowId].filter(
          (blockId) => blockId !== id
        );
      }
      delete innerBlocks[id];
    }

    for (const cell of addedCells) {
      const { rowId, index, id } = cell;
      if (!rowChanges[rowId]) {
        table.innerBlocks[rowId].splice(index, 0, id);
      }
      innerBlocks[id] = blockPass[id].block;
    }
  }

  for (const blockId of removedBlocks) {
    delete innerBlocks[blockId];
    const { block } = blockPass[blockId];
    if (!blockPass[block.parentId]) {
      const parent = innerBlocks[block.parentId];
      parent.blockOrder = parent.blockOrder.filter((id) => id !== blockId);
    }
  }

  for (const blockId of addedBlocks) {
    const { block, index } = blockPass[blockId];
    innerBlocks[blockId] = block;
    if (!blockPass[block.parentId]) {
      innerBlocks[block.parentId].blockOrder.splice(index, 0, blockId);
    }
  }

  for (const moved of movedBlocks) {
    const { id, from, to, toIndex, fromIndex } = moved;
    innerBlocks[id].parentId = from;

    if (from === to) {
      const parent = innerBlocks[from].blockOrder;
      const f = parent[fromIndex];
      parent[fromIndex] = parent[toIndex];
      parent[toIndex] = f;
      continue;
    }

    if (!blockPass[from]) {
      innerBlocks[from].blockOrder = innerBlocks[from].blockOrder.filter(
        (blockId) => blockId !== id
      );
    }

    if (!blockPass[to]) {
      innerBlocks[to].blockOrder.splice(toIndex, 0, id);
    }
  }

  if (shouldRenderResponsive) {
    state.mobile = table;
    state.tablet = table;
    if (device && device !== "desktop") {
      state[device] = toResponsive(state, device);
    }
  }
};
