interface SwitchControlProps {
  label?: string;
  checked: boolean;
  onChange?: (value: boolean) => void;
}

export default function SwitchControl({
  label,
  checked,
  onChange,
}: SwitchControlProps) {
  return (
    <label className="wptb-switch-control wptb-control">
      {label && <p className="wptb-switch-control-label">{label}</p>}
      <input
        type="checkbox"
        checked={checked}
        className="btn"
        onChange={() => onChange?.(!checked)}
      />
      <i />
    </label>
  );
}
