import { v4 as uuidv4 } from "uuid";
import {
  ResponsiveDeviceName,
  RowProps,
  TablesState,
  TableState,
  TableStateDevice,
} from "@wptb/dev/types";
import { CellProps, ResponsiveDeviceProps, TableProps } from "@wptb/dev/types";
import { TABLE_ID } from ".";
import { PayloadAction } from "@reduxjs/toolkit";
import notify from "@/public/notify";

const toColStack = (
  { innerBlocks, blockOrder }: TableStateDevice,
  { itemsPerHead }: ResponsiveDeviceProps,
  blocksPropsResponsive: Record<string, any>
): TableStateDevice => {
  const newBlockOrder: string[] = [];
  for (let i = 1; i < blockOrder.length; i++) {
    if ((i - 1) % itemsPerHead === 0) {
      newBlockOrder.push(blockOrder[0]);
      blocksPropsResponsive[blockOrder[0]] = {
        headerRow: true,
      };
    }
    newBlockOrder.push(blockOrder[i]);
  }
  return {
    innerBlocks,
    blockOrder: newBlockOrder,
  };
};

const toRowStack = (
  table: TableState,
  { itemsPerHead, topRowAsHeader }: ResponsiveDeviceProps,
  blocksPropsResponsive: Record<string, CellProps | RowProps>
): TableStateDevice => {
  const blocksProps = table.blocksProps;
  const newRowOrder: string[] = [];
  const newCellsOfRow: Record<string, string[]> = {};
  const header: Record<
    number,
    {
      id: string;
      rowspan: number;
      colspan: number;
    }
  > = {};
  const { blockOrder: rowOrder, innerBlocks: cellsOfRow } = table.desktop;

  const tableProps = blocksProps[TABLE_ID] as TableProps;
  const cols = tableProps.cols;

  let row = 0;
  if (topRowAsHeader) {
    itemsPerHead += 1;
    row++;
    const rowId = rowOrder[0];
    const rowProps: RowProps = blocksProps[rowId];
    const background =
      rowProps.background || tableProps.headerBg || tableProps.oddRowBg;

    const cells = cellsOfRow[rowId];
    for (let i = 0; i < cells.length; i++) {
      const id = cells[i];
      const props = blocksProps[id];

      if (props.rowspan >= itemsPerHead) {
        notify("Header exceeds the number of items per head.", "error");
        return {
          innerBlocks: cellsOfRow,
          blockOrder: rowOrder,
        };
      }

      header[props.col] = {
        id: id,
        rowspan: props.rowspan,
        colspan: props.colspan,
      };
      blocksPropsResponsive[id] = {
        ...props,
        rowspan: props.colspan,
        colspan: props.rowspan,
        background,
        hoverBg: tableProps.hoverHeaderBg || tableProps.hoverOddRowBg,
        highlight: rowProps.highlight,
        highlightScale: rowProps.highlightScale,
        isRowStack: true,
      } as CellProps;
    }
  }

  type QueueEntry =
    | {
        type: "cell";
        id: string;
        props: CellProps;
      }
    | {
        type: "space";
        data: number;
      }
    | {
        type: "filler";
        props: CellProps;
      };

  const rowsOfCols: string[][] = [];

  for (let i = 0; i < cols; i++) {
    rowsOfCols.push([]);
  }

  const headSpaceQueue: Record<number, number> = {};
  const colsCellQueue: Record<number, QueueEntry[]> = {};
  const nowFilling: Record<
    number,
    {
      hasFilled: number;
      cells: string[];
    }
  > = {};

  const endCol = cols - 1;

  const addEntryToRow = (col: number, queue: QueueEntry[]) => {
    while (queue.length > 0) {
      let activeRowForThisCol = nowFilling[col];
      if (
        !activeRowForThisCol ||
        activeRowForThisCol.hasFilled === itemsPerHead
      ) {
        /**
         * No row exists for this col, create a new row
         */
        const newRowId = uuidv4();
        rowsOfCols[col].push(newRowId);
        blocksPropsResponsive[newRowId] = {
          responsiveRow: true,
          edgeRow: col === endCol,
        } as RowProps;

        const cells: string[] = [];
        newCellsOfRow[newRowId] = cells;
        activeRowForThisCol = {
          hasFilled: 0,
          cells,
        };
        nowFilling[col] = activeRowForThisCol;
        const head = header[col];
        if (head) {
          /**
           * Add the header cell to the row
           */
          activeRowForThisCol.cells.push(head.id);
          activeRowForThisCol.hasFilled += head.rowspan;
          for (let i = 1; i < head.colspan; i++) {
            const futureCol = col + i;
            headSpaceQueue[futureCol] = head.rowspan;
          }
        }

        const headSpace = headSpaceQueue[col];
        if (headSpace) {
          activeRowForThisCol.hasFilled += headSpace;
          headSpaceQueue[col] = 0;
        }
      }

      const entry = queue.shift();
      if (!entry) {
        /**
         * Queue is empty
         * this should never happen
         */
        break;
      }

      const maxPossible = itemsPerHead - activeRowForThisCol.hasFilled;

      if (entry.type === "space") {
        /**
         * This space is occupied by preceding cell's colspan (now rowspan)
         */
        const spaceUsed = Math.min(entry.data, maxPossible);
        activeRowForThisCol.hasFilled += spaceUsed;
        if (spaceUsed < entry.data) {
          /**
           * There is still space left
           */
          queue.unshift({
            type: "space",
            data: entry.data - spaceUsed,
          });
        }
        if (activeRowForThisCol.hasFilled === itemsPerHead) {
          break;
        }
        continue;
      }

      if (entry.type === "filler") {
        /**
         * The cell that occupied this space was cut off due to max items per head
         * Let's keep it empty
         */
        const id = uuidv4();
        const props = entry.props;
        const spaceUsed = Math.min(props.rowspan, maxPossible);
        activeRowForThisCol.cells.push(id);
        activeRowForThisCol.hasFilled += spaceUsed;

        blocksPropsResponsive[id] = {
          ...props,
          rowspan: props.colspan,
          colspan: spaceUsed,
        };
        if (spaceUsed < props.rowspan) {
          /**
           * There is still space left
           */
          props.rowspan -= spaceUsed;
          queue.unshift({
            type: "filler",
            props,
          });
        }
        if (activeRowForThisCol.hasFilled === itemsPerHead) {
          break;
        }
        continue;
      }

      const id = entry.id;
      const props = entry.props;
      const spaceUsed = Math.min(props.rowspan, maxPossible);
      activeRowForThisCol.cells.push(id);
      activeRowForThisCol.hasFilled += spaceUsed;

      blocksPropsResponsive[id] = {
        ...props,
        rowspan: props.colspan,
        colspan: spaceUsed,
      };

      for (let k = 1; k < props.colspan; k++) {
        const futureCol = col + k;
        let futureColQueue = colsCellQueue[futureCol];
        if (!futureColQueue) {
          futureColQueue = [];
          colsCellQueue[futureCol] = futureColQueue;
        }
        futureColQueue.unshift({
          type: "space",
          data: props.rowspan,
        });
      }

      if (spaceUsed < props.rowspan) {
        props.rowspan -= spaceUsed;
        queue.unshift({
          type: "filler",
          props,
        });
      }

      if (activeRowForThisCol.hasFilled === itemsPerHead) {
        break;
      }
    }
  };

  for (; row < rowOrder.length; row++) {
    const rowId = rowOrder[row];
    const rowProps: RowProps = blocksProps[rowId];
    let background = rowProps.background;

    if (row % 2 === 0) {
      background ||= tableProps.oddRowBg;
    } else {
      background ||= tableProps.evenRowBg;
    }

    const cells = cellsOfRow[rowId];

    let lastCol = 0;

    for (let j = 0; j < cells.length; j++) {
      const cellId = cells[j];
      const cellProps: CellProps = {
        ...blocksProps[cellId],
        background,
        isRowStack: true,
      };
      const col = cellProps.col;
      for (let k = lastCol + 1; k < col; k++) {
        const queue = colsCellQueue[k];
        if (queue) {
          addEntryToRow(k, queue);
        }
      }
      let queue = colsCellQueue[col];
      if (!queue) {
        queue = [];
        colsCellQueue[col] = queue;
      }

      queue.push({
        type: "cell",
        id: cellId,
        props: cellProps,
      });
      addEntryToRow(col, queue);
      lastCol = col;
    }

    for (let k = lastCol + 1; k < cols; k++) {
      const queue = colsCellQueue[k];
      if (queue) {
        addEntryToRow(k, queue);
      }
    }
  }

  const colRowGotIdx = Array(cols).fill(0);
  let hasMore = true;
  while (hasMore) {
    hasMore = false;
    for (let col = 0; col < cols; col++) {
      const idx = colRowGotIdx[col];
      if (idx < rowsOfCols[col].length) {
        hasMore = true;
        colRowGotIdx[col]++;
        newRowOrder.push(rowsOfCols[col][idx]);
      }
    }
  }

  return {
    innerBlocks: newCellsOfRow,
    blockOrder: newRowOrder,
  };
};

export const internalSetResponsive = (
  tables: TablesState,
  action: PayloadAction<{
    device: ResponsiveDeviceName;
    tableId: string;
  }>
) => {
  const { device, tableId } = action.payload;
  const state = tables[tableId];
  state.blocksPropsResponsive = {};
  if (device === "desktop") {
    return;
  }
  let table = state.desktop;
  const mainConfig: TableProps["responsive"] =
    state.blocksProps[TABLE_ID]?.responsive;
  const config = mainConfig?.[device];

  if (!mainConfig?.enable || config?.disable) {
    state[device] = table;
    return;
  }

  const blocksPropsResponsive = {};
  if (config?.stackDirection === "row") {
    table = toRowStack(state, config, blocksPropsResponsive);
  } else if (config?.topRowAsHeader) {
    table = toColStack(table, config, blocksPropsResponsive);
  }
  state.blocksPropsResponsive = blocksPropsResponsive;
  state[device] = table;
};

export const toResponsive = (
  state: TableState,
  device: ResponsiveDeviceName
): TableStateDevice => {
  let table = state.desktop;
  if (device === "desktop") {
    return table;
  }
  const mainConfig: TableProps["responsive"] =
    state.blocksProps[TABLE_ID]?.responsive;
  const config = mainConfig?.[device];

  if (!mainConfig?.enable || config?.disable) {
    return table;
  }
  const blocksPropsResponsive = {};
  if (config?.stackDirection === "row") {
    table = toRowStack(state, config, blocksPropsResponsive);
  } else if (config?.topRowAsHeader) {
    table = toColStack(table, config, blocksPropsResponsive);
  }
  state.blocksPropsResponsive = blocksPropsResponsive;
  return table;
};
