import store from "@/store";
import { BlockState, TableState } from "@wptb/dev/types";
import BuilderCtx from "@/builder/ctx";

interface CellChange {
  id: string;
  rowId: string;
  index: number;
}

interface RowChange {
  id: string;
  index: number;
  blockOrder: string[];
}

export interface TimeTravelEntry {
  shouldRenderResponsive: boolean;
  addedProps: Record<string, any>;
  removedProps: Record<string, any>;
  updatedProps: Record<
    string,
    {
      from: any;
      to: any;
    }
  >;
  blockPass: Record<
    string,
    {
      index: number;
      block: BlockState;
    }
  >;
  addedBlocks: string[];
  removedBlocks: string[];
  movedBlocks: {
    id: string;
    from: string;
    to: string;
    fromIndex: number;
    toIndex: number;
  }[];
  addedRows: string[];
  removedRows: string[];
  rowChanges: Record<string, RowChange>;
  addedCells: CellChange[];
  removedCells: CellChange[];

  rowOrder?: {
    from: string[];
    to: string[];
  };
  colOrder?: {
    from: Record<string, string[]>;
    to: Record<string, string[]>;
  };
}

export default class TimeTravelCapturer {
  private state: TableState;

  private propsChange: string[] = [];

  private blockChange: string[] = [];
  private movedBlocks: {
    id: string;
    from: string;
    to: string;
  }[] = [];

  private addedRows: string[] = [];
  private removedRows: string[] = [];

  private addedCells: {
    cellId: string;
    rowId: string;
  }[] = [];
  private removedCells: {
    cellId: string;
    rowId: string;
  }[] = [];

  private rowOrder?: boolean;
  private colOrder?: boolean;

  static init(): TimeTravelCapturer {
    return new TimeTravelCapturer(store.getState().table[BuilderCtx.TABLE_ID!]);
  }

  constructor(state: TableState) {
    this.state = state;
  }

  props(blockId: string) {
    this.propsChange.push(blockId);
  }

  block(blockId: string) {
    this.blockChange.push(blockId);
  }

  addRow(id: string) {
    this.addedRows.push(id);
  }
  removeRow(id: string) {
    this.removedRows.push(id);
  }

  rowOrderChanged() {
    this.rowOrder = true;
  }

  colOrderChanged() {
    this.colOrder = true;
  }

  addCell(cellId: string, rowId: string) {
    this.addedCells.push({
      cellId,
      rowId,
    });
  }
  removeCell(cellId: string, rowId: string) {
    this.removedCells.push({
      cellId,
      rowId,
    });
  }

  moveBlock(blockId: string, from: string, to: string) {
    this.movedBlocks.push({
      id: blockId,
      from,
      to,
    });
  }

  getChanges(shouldRenderResponsive: boolean): TimeTravelEntry {
    const newState = store.getState().table[BuilderCtx.TABLE_ID!];
    const addedProps: Record<string, any> = {};
    const removedProps: Record<string, any> = {};
    const updatedProps: Record<
      string,
      {
        from: any;
        to: any;
      }
    > = {};

    const oldProps = this.state.blocksProps;
    const newProps = newState.blocksProps;

    for (let i = 0; i < this.propsChange.length; i++) {
      const blockId = this.propsChange[i];
      if (!newProps[blockId]) {
        removedProps[blockId] = oldProps[blockId];
      } else if (!oldProps[blockId]) {
        addedProps[blockId] = newProps[blockId];
      } else {
        updatedProps[blockId] = {
          from: oldProps[blockId],
          to: newProps[blockId],
        };
      }
    }

    const oldBlocks: Record<string, BlockState> = this.state.innerBlocks;
    const newBlocks: Record<string, BlockState> = newState.innerBlocks;

    const blockPass: TimeTravelEntry["blockPass"] = {};
    const addedBlocks: string[] = [];
    const removedBlocks: string[] = [];

    for (let i = 0; i < this.blockChange.length; i++) {
      const blockId = this.blockChange[i];
      if (!newBlocks[blockId]) {
        const block = oldBlocks[blockId];
        removedBlocks.push(blockId);
        removedProps[blockId] = oldProps[blockId];
        const parent = oldBlocks[block.parentId];
        if (!parent) {
          continue;
        }
        blockPass[blockId] = {
          block,
          index: parent.blockOrder.indexOf(blockId),
        };
      } else {
        const block = newBlocks[blockId];
        addedBlocks.push(blockId);
        addedProps[blockId] = newProps[blockId];
        const parent = newBlocks[block.parentId];
        if (!parent) {
          continue;
        }
        blockPass[blockId] = {
          block,
          index: parent.blockOrder.indexOf(blockId),
        };
      }
    }

    const oldTable = this.state.desktop;
    const newTable = newState.desktop;

    const addedRows: string[] = [];
    const removedRows: string[] = [];
    const rowChanges: Record<string, RowChange> = {};

    for (let i = 0; i < this.addedRows.length; i++) {
      const id = this.addedRows[i];
      addedProps[id] = newProps[id];
      const index = newTable.blockOrder.indexOf(id);
      addedRows.push(id);
      rowChanges[id] = {
        id,
        index,
        blockOrder: newTable.innerBlocks[id],
      };
    }

    for (let i = 0; i < this.removedRows.length; i++) {
      const id = this.removedRows[i];
      removedProps[id] = oldProps[id];
      const index = oldTable.blockOrder.indexOf(id);
      removedRows.push(id);
      rowChanges[id] = {
        id,
        index,
        blockOrder: oldTable.innerBlocks[id],
      };
    }

    const addedCells: CellChange[] = [];
    const removedCells: CellChange[] = [];

    for (let i = 0; i < this.removedCells.length; i++) {
      const { cellId, rowId } = this.removedCells[i];
      const cellIdx = oldTable.innerBlocks[rowId].indexOf(cellId);
      removedCells.push({
        id: cellId,
        rowId,
        index: cellIdx,
      });
      removedProps[cellId] = oldProps[cellId];
      blockPass[cellId] = {
        block: oldBlocks[cellId],
        index: cellIdx,
      };
    }

    for (let i = 0; i < this.addedCells.length; i++) {
      const { cellId, rowId } = this.addedCells[i];
      const cellIdx = newTable.innerBlocks[rowId].indexOf(cellId);
      addedCells.push({
        id: cellId,
        rowId,
        index: cellIdx,
      });
      addedProps[cellId] = newProps[cellId];
      blockPass[cellId] = {
        block: newBlocks[cellId],
        index: cellIdx,
      };
    }

    const movedBlocks: TimeTravelEntry["movedBlocks"] = [];
    for (let i = 0; i < this.movedBlocks.length; i++) {
      const { id, from, to } = this.movedBlocks[i];
      const fromIndex = oldBlocks[from].blockOrder.indexOf(id);
      const toIndex = newBlocks[to].blockOrder.indexOf(id);
      movedBlocks.push({
        id,
        from,
        to,
        fromIndex,
        toIndex,
      });
    }

    const rowOrder = this.rowOrder
      ? {
          from: oldTable.blockOrder,
          to: newTable.blockOrder,
        }
      : undefined;

    const colOrder = this.colOrder
      ? {
          from: oldTable.innerBlocks,
          to: newTable.innerBlocks,
        }
      : undefined;

    return {
      addedProps,
      removedProps,
      updatedProps,
      blockPass,
      addedBlocks,
      removedBlocks,
      movedBlocks,
      addedCells,
      removedCells,
      removedRows,
      addedRows,
      rowChanges,
      rowOrder,
      colOrder,
      shouldRenderResponsive,
    };
  }
}
